import { Box, Button } from "@mui/material";
import React from "react";
import { changeTheme } from "../store/store.tsx";


const ZustandChild3 = () => {
  const { dark, darkTheme} = changeTheme();
  return (
    <div style={{backgroundColor: dark ? 'pink' : 'brown', height: '15vh',margin: '10px 0'}}>
      {dark && <h1>this is child component 3</h1>}
      {!dark && <h1> no more useContext</h1>}

    </div>
  );
};

export default ZustandChild3;
