import React, { useState } from "react";
import Main from "../components/main/main.js";
import { Box, Button, Typography, TextField } from "@mui/material";
import { useNavigate } from "react-router-dom";
import { changeTheme } from "../store/store.tsx";

function UseState() {
  const navigate = useNavigate();
  const initial = 20;
  const [name, setName] = useState("mario");
  const [count, setCount] = useState(initial);
  const [newName, setNewName] = useState({ firstName: "", lastName: "" });
  const {dark} = changeTheme()
  

  const minusCount = () => {
    setCount(count - 1);
  };

  const addFive = () => {
    setCount((prevCount) => prevCount + 5);
  };
  const reset = () => {
    setCount(initial);
  };
  const addCount = () => {
    setCount(count + 20);
  };
  const handleName = () => {
    setName("luigi");
  };
  return (
    <Main>
      <Box sx={{ marginBottom: "20px", color: dark ? '' : 'white' }}>
        <Box textAlign={"center"}>
          <Typography variant="h2" color="black"></Typography>
          UseState
        </Box>
        <Box>{name}</Box>
        <Button variant="contained" color="secondary" onClick={handleName}>
          change name
        </Button>
        <Button
          variant="contained"
          color="primary"
          onClick={() => setName("kupa")}
        >
          change name
        </Button>
      </Box>
      <Box >
        <Typography variant="body1" color="initial">
          {count}
        </Typography>
        <Button
          variant="contained"
          color="secondary"
          onClick={() => setCount(count + 1)}
        >
          count
        </Button>
        <Button variant="contained" color="success" onClick={addCount}>
          new count
        </Button>
        <Button variant="contained" color="error" onClick={minusCount}>
          minus count
        </Button>
        <Button variant="contained" color="error" onClick={addFive}>
          Add Five
        </Button>
        <Button variant="contained" color="error" onClick={reset}>
          reset
        </Button>
      </Box>
      <br />
      <Box>
        <Typography variant="h6" color="initial" component="p" sx={{color: dark ? 'black' : 'white' }}>
          Scenario: <br />
          In a React component, you often need to manage and update state—pieces
          of data that can change over time based on user interactions, API
          calls, or other events. <br /> <br />
          Problem: <br /> How do you keep track of and update this state in a
          functional component? <br /> <br />
          Solution: <br />
          useState is a React hook that provides a way to declare state
          variables in functional components. Basic Usage:
        </Typography>
      </Box>
      <Box>
        <img src="image/useState.png" />
        <img src="image/useState2.png" />
      </Box>
      <Box >
        <Typography variant="h6" color="initial" component="p" sx={{color: dark ? 'black' : 'white' }}>
          Explanation:<br /> useState(0) initializes the state variable count with an
          initial value of 0. setCount is a function provided by useState to
          update the count state. When the button is clicked, it increments the
          count using the setCount function.<br /><br /> Key Points:<br /> useState is used to
          declare state variables in functional components. It takes an initial
          value as an argument and returns an array with the current state value
          and a function to update the state. The state is preserved between
          renders, and when the state is updated, React will re-render the
          component with the new state. <br /><br />In simple terms, useState helps you
          manage and update state in your React functional components, making
          them dynamic and interactive.
        </Typography>
      </Box>
    </Main>
  );
}

export default UseState;
