import { Box, Button } from "@mui/material";
import React from "react";
import { changeTheme } from "../store/store.tsx";


const ZustandChild4 = () => {
  const { dark, darkTheme} = changeTheme();
  return (
    <Box style={{backgroundColor: dark ? 'pink' : 'gray', height: '15vh',margin: '10px 0'}}>
      {dark && <h1>this is child component 4</h1>}
      {!dark && <h1> no more! no more!</h1>}

    </Box>
  );
};

export default ZustandChild4;
