import React, { useState } from "react";
import {
  AppBar,
  Box,
  InputBase,
  Menu,
  MenuItem,
  Toolbar,
  Typography,
} from "@mui/material";
import styled from "@emotion/styled";
import PetsIcon from "@mui/icons-material/Pets";
import { changeTheme } from "../store/store.tsx";

import { theme } from "../theme";
import Badge from "@mui/material/Badge";
import MailIcon from "@mui/icons-material/Mail";
import NotificationsIcon from "@mui/icons-material/Notifications";
import Avatar from "@mui/material/Avatar";
import LightModeIcon from "@mui/icons-material/LightMode";
import DarkModeIcon from "@mui/icons-material/DarkMode";

const StyledToolBar = styled(Toolbar)({
  display: "flex",
  justifyContent: "space-between",
  width: "100%",
});

const CustomSearch = styled("div")(({ theme }) => ({
  backgroundColor: "white",
  padding: "0 10px",
  borderRadius: theme.shape.borderRadius,
  width: "40%",
}));

const Icons = styled(Box)(({ theme }) => ({
  display: "flex",
  alignItems: "center",
  gap: 20,
}));

const UserMobileView = styled(Box)(({ theme }) => ({
  display: "flex",
  alignItems: "center",
  gap: 10,
  [theme.breakpoints.up("sm")]: {
    display: "none",
  },
}));
function Navbar() {
  const [open, setopen] = useState(false);
  const { dark, darkTheme } = changeTheme();

  return (
    <AppBar position="sticky" sx={{backgroundColor: dark ? '#1976d2' : 'black'}}>
      <Toolbar>
        <StyledToolBar>
          <Typography
            variant="h6"
            sx={{ display: { xs: "none", sm: "block" } }}
          >
            React JS Ralph
          </Typography>
          <PetsIcon sx={{ display: { xs: "block", sm: "none" } }} />
          <CustomSearch>
            <InputBase placeholder="Search...." />
          </CustomSearch>

          <Icons>
            <Box onClick={darkTheme} sx={{cursor: 'pointer'}}>
              {dark ? <DarkModeIcon /> : <LightModeIcon />}
            </Box>
            <Badge
              badgeContent={4}
              color="error"
              sx={{ display: { xs: "none", sm: "block" } }}
            >
              <MailIcon />
            </Badge>
            <Badge
              badgeContent={4}
              color="error"
              sx={{ display: { xs: "none", sm: "block" } }}
            >
              <NotificationsIcon />
            </Badge>
            <Avatar
              sx={{
                width: 30,
                height: 30,
                display: { xs: "none", sm: "block" },
              }}
              alt="Remy Sharp"
              src="./image/cat.jpg"
              onClick={() => {
                setopen(true);
              }}
            />

            <UserMobileView>
              <Avatar
                sx={{ width: 30, height: 30 }}
                alt="Remy Sharp"
                src="/static/images/avatar/1.jpg"
                onClick={() => {
                  setopen(true);
                }}
              />
              <Typography variant="h6">Ralph</Typography>
            </UserMobileView>
          </Icons>
        </StyledToolBar>
      </Toolbar>
      <Menu
        id="demo-positioned-menu"
        aria-labelledby="demo-positioned-button"
        open={open}
        onClose={() => setopen(false)}
        anchorOrigin={{
          vertical: "top",
          horizontal: "right",
        }}
        transformOrigin={{
          vertical: "top",
          horizontal: "right",
        }}
      >
        <MenuItem>Profile</MenuItem>
        <MenuItem>My account</MenuItem>
        <MenuItem>Logout</MenuItem>
      </Menu>
    </AppBar>
  );
}

export default Navbar;
